.catalog-wrapper {
    // display: none
    position: relative;
    padding: 20px 15px 70px;
    color: #231e1f;
    text-align: left;
    font-size: 14px;
    @media(min-width:576px) {
        padding: 20px 35px 70px; }
    @media(min-width:992px) {
        padding: 20px 30px 40px; }
    @media(min-width:1200px) {
        padding: 20px 70px 40px; }
    .flex {
        justify-content: space-between;
        margin-top: 20px;
        @media(min-width:992px) {
            margin-top: 30px; } } }
.path {}
.section-name {
    width: 90%;
    margin: 0 auto; }

.catalog {
    width: 100%; }
.catalog-header {
    color: #666666;
    margin: 0 auto;
    width: 90%;
    .card-tabs {
        margin: 0;
        span {
            font-weight: 600;
            font-size: 14px; } }
    .card-tab {
        margin-right: 20px; }
    .flex {
        margin: 0;
        align-items: center;
 }        // justify-content: space-around
    @media(min-width:992px) {
        width: 100%; } }

.btn-filtr {
    width: 160px;
    cursor: pointer;
    padding: 9px 30px 9px 50px;
    background-color: #b8a4dd;
    border-radius: 20px;
    color: #fff;
    font-size: 16px;
    margin-bottom: 30px; }
.card-tabs-wrapper {}
.catalog-sort {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 5px;
    font-size: 16px;
    @media(min-width:992px) {
        font-size: 14px;
        justify-content: flex-end; } }
.catalog-dropdown {
    position: relative;
    color: #666666;
    font-weight: 400; }
.dropdown-toggle {
    font-weight: 700;
    margin-left: 20px;
    cursor: pointer;
    img {
        margin-left: 8px; } }
.dropdown-menu {
    display: none;
    position: absolute;
    right: 20px;
    transform: translateX(50%);
    margin-top: 10px;
    background-color: #fff;
    border: 1px solid #c6c6c6;
    border-radius: 5px;
    padding: 5px;
    z-index: 2;
    text-align: center;
    font-weight: 700;
    li {
        margin-bottom: 5px; } }

.catalog-view {
    margin-left: 30px; }
#wiew-line {
    margin-right: 5px; }
.catalog-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 25px;
    max-width: 890px;
    margin-bottom: 65px;
    @media(min-width:992px) {
        margin-bottom: 0; }
    @media(min-width:1050px) {
        margin-top: 55px;
        justify-content: flex-start; }
    .product-item {
        margin: 0;
        width: 225px;

        @media(min-width:1050px) {
            width: 250px;
            margin-bottom: 20px; }
        .product-price_num {
            font-size: 12px; } } }
#catalog-items-listWiew {
    display: none;
    margin-top: 25px;
    max-width: 890px;
    margin-bottom: 65px;
