        margin: 0 auto;
        @media(min-width:768px) {
            flex-direction: row;
            padding: 0 20px;
            margin-bottom: 10px; }
        .product-item-text {
            @media(min-width:768px) {
                width: 300px;
                align-self: center; } } }
    .product-img {
        margin: 10px 0 0 0;
        width: 170px;
