            order: -1;
            margin: 0 20px 0 0; }

        img {
            // display: block
            // margin: 0 auto
            max-width: 100%;
            max-height: 100%;
            @media(min-width:768px) {
                max-height: 100%;
                max-width: 100%; } } }

    .btn-label, .btn-hit {
        // position: absolute
        left: -15px;
