        // border-radius: 10px
        // border: none
        // height: 26px
        // font-size: 12px
        // font-weight: 700
        // padding: 5px
        // text-align: center
 }        // padding-bottom: 3px
    .btn-label {
        // background-color: $red
        // width: 72px
 }        // top: 5px
    .btn-hit {
        // background-color: #f9b233
        // width: 52px
 }        // top: 35px
    .product-code {
        color: #c6c6c6;
        font-size: 13px;
        font-weight: 400;
        margin-bottom: 10px;
        @media(min-width:768px) {
            margin: 20px 0; } }
    .product-title {
        width: 100%;
        font-size: 16px;
        font-weight: 700;
        margin: 5px 0 10px; }
    // .product-rating
    //     display: flex
    //     align-items: center
