    //     text-decoration: underline
    .product-bottom {
        position: absolute;
        bottom: 20px;
        right: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        align-items: flex-end;
        color: #3c3c3b;
        font-weight: 700;
        margin-top: 20px;
        width: 150px;
        @media(min-width:768px) {
            margin-top: 30px;
