        padding-left: 5px;
        padding-bottom: 3px; } }

.show-more-block {
    margin-top: 160px;
    img {
        margin: 0 auto;
        display: block;
        margin-bottom: 20px; } }
.btn-more {
    width: 147px;
    height: 34px;
    background-color: $purple;
    color: #ffffff;
    border-radius: 10px;
    border: none;
    font-weight: 700;
    font-size: 16px;
    margin: 0 auto;
    display: block; }
.catalog-bottom {
    display: none;
    margin-top: 50px;
    @media(min-width:992px) {
        display: flex;
        justify-content: space-between; }
    .page-pagination {
        text-align: center; } }

