.product-slider-wrapper {
    margin: 50px 35px;
    @media(min-width:768px) {
        margin: 70px 55px 40px; }
    @media(min-width:1200px) {
        margin: 70px 85px 40px; }
    h2 {
        text-align: left;
        margin: 0 0 30px 0;
        font-size: 26px;
        @media(min-width:768px) {
            font-size: 24px; }
        @media(min-width:1100px) {
            margin: 0 35px 3px 0; } }

    > .flex {
        margin-bottom: 45px;
        flex-direction: column;
        align-items: flex-start;
        @media(min-width:992px) {
            margin-bottom: 25px; }
        @media(min-width:1100px) {
            flex-direction: row;
            align-items: center; } }
    .card-tabs {
        margin: 0;
        flex-direction: row;
        flex-wrap: wrap; }

    .card-tab {
        margin: 0 30px 10px 0;
        @media(min-width:1100px) {
            margin: 0 30px 0 0; }
        span {
            font-size: 16px;
            @media(min-width:1100px) {
                font-size: 14px; } }
        &:nth-child(2) {
            @media(min-width:560px) {
                margin-right: 120px; }
            @media(min-width:640px) {
                margin-right: 30px; } } } }
.product {
    outline: none;
    margin: 0 auto; }

.product-item {
    width: 330px;
    text-align: left;
    padding: 5px 12px 15px;
    border: 1px solid #c6c6c6;
    margin: 0 auto;
    @media(min-width:768px) {
        width: 250px; }
    .product-img {
        // border-bottom: 1px solid #c6c6c6
        margin-bottom: 15px;
        height: 280px;
        position: relative;
        img {
            display: block;
            margin: 0 auto;
            max-width: 100%;
            @media(min-width:768px) {
                max-height: 200px;
                max-width: 100%; } } }

    .btn-label, .btn-hit {
        position: absolute;
        color: #fff;
        border-radius: 10px;
        border: none;
        height: 26px;
        font-size: 12px;
        font-weight: 700;
        padding: 5px;
        text-align: center;
        padding-bottom: 3px; }
    .btn-label {
        background-color: $red;
        width: 72px;
        top: 5px; }
    .btn-hit {
        background-color: #f9b233;
        width: 52px;
        top: 35px; }
    .product-code {
        color: #c6c6c6;
        font-size: 11px;
        font-weight: 400; }
    .product-title {
        width: 200px;
        font-size: 13px;
        font-weight: 700;
        margin: 5px 0; }
    .product-rating {
        display: flex;
        align-items: center; }
    .product-stars {
        margin-right: 10px; }
    .product-comments {
        color: #0091d6;
        font-size: 11px;
        font-weight: 400;
        text-decoration: underline; }
    .product-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #3c3c3b;
        font-weight: 700;
        margin-top: 20px; }
    .product-price {
        flex-grow: 2; }
    .product-price_num {
        font-size: 25px;
        @media(min-width:768px) {
            font-size: 19px; } }
    .product-price_cur {
        font-size: 13px; }

    .product-like {
        width: 22px;
        margin-right: 10px;
        @media(min-width:768px) {
            width: auto; } } }
.product-buy {
    background-color: $green;
    color: #fff;
    border-radius: 12px;
    border: none;
    width: 88px;
    height: 35px;
    font-size: 19px;
    font-weight: 700;
    padding-left: 5px;
    padding-bottom: 3px; }

.product-slider-prev, .product-slider-next {
    position: absolute;
    top: 50%; }
.product-slider-prev {
    left: 0px;
    transform: translate(-50%, -50%); }
.product-slider-next {
    right: 0px;
    transform: translate(50%, -50%); }
