// ::::::::::::: bg-popup:::::::::::::
.bg-popup {
	position: absolute;
	display: none;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: rgba(151,151,151, .7);
	z-index: 9;
	@media(min-width:992px) {
 } }		// display: none!important

.popup {
	width: 338px;
	padding-top: 45px;
	border-radius: 10px;
	background: #fff;
	font-weight: 700;
	position: fixed;
	top: 50%;
	left: 50%;
	transform:  translate(-50%, -50%);
	z-index: 11;
	box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.2); }
.popup-close {
	position: absolute;
	top: 15px;
	right: 15px;
	padding: 10px;
	cursor: pointer; }
.bg_popup-img {
	position: absolute;
	top: 57px;
	left: 0px;
	z-index: -1;
	margin: 0 10px; }
.popup_title {
	color: #231e1f;
	font-size: 26px;
	line-height: 1.2;
	width: 265px;
	text-align: center;
	margin: 0 auto;
	span {
		font-weight: 600; } }
.popup_subtitle {
	color: #231e1f;
	margin-bottom: 10px; }
.form-input, .form-textarea {
	width: 270px;
	border: 1px solid #c6c6c6;
	border-radius: 10px; }
.form-input {
	height: 40px;
	margin-bottom: 15px; }
.form-textarea {
	height: 90px; }

.form-submit {
	display: block;
	width: 270px;
	font-size: 14px;
	font-weight: 700;
	color: #fff;
	border-radius: 10px;
	border: none;
	padding: 10px 20px;
	margin: 0 auto; }

.popup_discount {
	display: none;
	height: 325px;
	.form-submit {
		margin-bottom: 40px;
		background-color: #b8a4dd;
		&:focus, &:active, &:hover {
			background-color: darken(#b8a4dd, 10%); } } }

.popup_recall {
	display: none;
	padding-bottom: 50px;
	.popup_title {
		margin-bottom: 35px; }
	.form-submit {
		// margin-bottom: 50px
		background-color: $red;
		&:focus, &:active, &:hover {
			background-color: darken($red, 10%); } } }

#captcha {
	margin: 5px auto 17px;
	a {
		background-color: #dadada;
		padding: 25px 20px;
		color:#666666 {}

		&:focus, &:active, &:hover {
			background-color: darken(#dadada, 10%); } } }

.popup_enter {
	display: none;
	.form-submit {
		// margin-bottom: 50px
		background-color: $green;
		&:focus, &:active, &:hover {
			background-color: darken($green, 10%); } }
	.btn_sosial-wrapper {
		width: 300px;
		margin: 0px auto 35px;
		display: flex;
		justify-content: space-between;
		.btn_sosial {
			width: 143px;
			margin: 0;
			font-weight: 500;
			font-size: 12px; }
		#btn_facebook {
			background-color: #3b5a94;
			&:focus, &:active, &:hover {
				background-color: darken(#3b5a94, 10%); } }
		#btn_google {
			background-color: #f9463e;
			&:focus, &:active, &:hover {
				background-color: darken(#f9463e, 10%); } } }
	#psw_restore {
		display: inline-block;
		font-size: 12px;
		color: #231e1f;
		padding: 10px;
		cursor: pointer; }
	.registration {
		height: 75px;
		border-top: 1px solid #919191;
		padding-top: 20px;
		span {
			color: $green;
			text-transform: uppercase;
			padding: 10px;
			cursor: pointer;
			&:focus, &:active, &:hover {
				color: darken($green, 10%); } } } }
.popup_registr {
	display: none;
	.form-submit {
		// margin-bottom: 50px
		background-color: $green;
		text-transform: uppercase;
		&:focus, &:active, &:hover {
			background-color: darken($green, 10%); } }
	.popup_title {
		margin-bottom: 20px; }
	.popup_bottom {
		font-size: 12px;
		margin: 20px 0 30px;
		display: inline-block;
		color: #333; }
	#modalEnter {
		color: #0089d5;
		margin-left: 20px;
		text-decoration: underline;
		text-decoration-style: dotted;
		cursor: pointer; } }
.popup_psw {
	display: none;
	.form-submit {
		background-color: $green;
		text-transform: uppercase;
		margin-bottom: 40px;
		&:focus, &:active, &:hover {
			background-color: darken($green, 10%); } }
	.popup_title {
		margin-bottom: 20px; } }
.popup_basket {
	display: none;
	padding: 0;
	width: 480px;
	border-radius: 15px;
	@media(min-width:640px) {
		width: 600px; } }
.popup_basket-header {
	background-color: #cde9f8;
	color: #000000;
	font-size: 24px;
	font-weight: 700;
	padding: 20px;
	border-radius: 15px	15px 0 0;
	position: relative;
	.bg-left {
		position: absolute;
		left: 20px;
		bottom: 0; }
	.bg-right {
		right: 50px;
		position: absolute;
		bottom: 0; }
	.popup-close {
		top: 5px;
		right: 5px; } }
.popup_basket-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 30px 60px 20px 20px;
	position: relative;
	.basket-item_close {
		right: 20px;
		margin-top: 2px;
		cursor: pointer; } }
.popup_basket-item_img {
	width: 58px;
	height: 58px;
	border: 1px solid #919191;
	border-radius: 2px;
	margin-right: 15px;
	img {
		// width: 100%
		height: 100%; } }

.basket-item_title {
	text-align: left;
	color: #000000;
	font-size: 14px;
	font-weight: 700;
	flex-grow: 1; }

.basket-item_price {
	margin-left: 30px; }
.basket-item_count {}


.popup_basket-total {
	margin: 0 30px;
	padding: 20px 30px 0;
	border-top: 1px solid #919191;
	border-bottom: 1px solid #919191;

	.price {
		margin-bottom: 20px;
		// text-align: left
		display: flex;
		justify-content: space-between;
		span {
			color: #666666;
			font-size: 13px;
			font-weight: 400; }
		strong, .total-goods-num {
			color: #000000;
			font-size: 14px;
			font-weight: 700; } } }
.popup_basket-message {
	background-color: #f7f4ec;
	padding: 20px 35px;
	margin: 20px 0 30px;
	text-align: left;
	color: #666666;
	font-size: 13px;
	font-weight: 400;
	span {
		color: #63c6e7; } }
.popup_basket-buttons {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 30px;
	.basket_submit {
		background-color: $green; }
	input {
		width: 160px;
		height: 34px;
		margin: 0 10px 0 0;
		background-color: #c6c6c6;
		padding-top: 8px; }
	.continue {
		margin: 0;
		cursor: pointer;
		font-weight: 400;
		font-size: 13px; } }

